import React from 'react';
import styled from 'styled-components';

const Button = styled.button`
  border: 0px;

  background-color: ${({ theme }) => theme.redFirst};
`;
const Textarea = styled.textarea`
  background-color: ${({ theme }) => theme.graySecond};
  height: 120px;
`;
const InputEmail = styled.input`
  background-color: ${({ theme }) => theme.graySecond};
`;
const InputName = styled.input`
  background-color: ${({ theme }) => theme.redThird};
`;
const Form = styled.form`
  display: flex;
  width: 100%;
  min-height: 75vh;
  margin: 5vh 0px 0px 0px;
  flex-direction: column;
  justify-content: space-around;

  color: black;

  input,
  textarea,
  button {
    min-height: 40px;
    border: none;
    font-size: ${({ theme }) => theme.font.m};

    font-family: 'Baloo Tamma', cursive;
  }
  input::placeholder,
  textarea::placeholder {
    color: black;
    text-transform: uppercase;
  }
`;
const Wrapper = styled.section`
  width: 80%;
  margin: 0px auto;
`;
const Kontakt = () => {
  return (
    <Wrapper>
      <Form
        name="portfolioContact"
        method="post"
        data-netlify="true"
        data-nelify-honeypot="bot-field"
        method="post"
      >
        <InputName type="text" name="name" placeholder="Imie:" />
        <InputEmail type="email" name="email" placeholder="Email:" />
        <Textarea nmae="message" placeholder="Treść wiadomości" />
        <Button>Wyślij</Button>
      </Form>
    </Wrapper>
  );
};

export default Kontakt;
